
//==================== WEBFONTS: ==================
//==================================================

// #Poppins
@font-face {
  font-family: 'Poppins';
  src: url('#{$path-theme}/fonts/Poppins-Regular.woff2') format('woff2'),
       url('#{$path-theme}/fonts/Poppins-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Poppins';
  src: url('#{$path-theme}/fonts/Poppins-Medium.woff2') format('woff2'),
       url('#{$path-theme}/fonts/Poppins-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Poppins';
  src: url('#{$path-theme}/fonts/Poppins-SemiBold.woff2') format('woff2'),
       url('#{$path-theme}/fonts/Poppins-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
    font-family: 'Butler';
    src: url('#{$path-theme}/fonts/Butler-Bold.woff2') format('woff2'),
        url('#{$path-theme}/fonts/Butler-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Freight';
    src: url('#{$path-theme}/fonts/Freight-TextBookItalic.woff2') format('woff2'),
        url('#{$path-theme}/fonts/Freight-TextBookItalic.woff') format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}

// #Global
h1,h2,h3,h4,h5,h6,
.h1,.h2,.h3,.h4,.h5,.h6 {
  font-family: $hfont;
}

h1,.h1 {
}

h2,.h2 {
  font-size: 84px;
  line-height: 1;
  padding-bottom: 40px;

  @include mq(phone-wide,max) {
    font-size: 70px;
  }
}

h3,.h3 {
  font-size: 62px;
  line-height: 1.16;
  padding-bottom: 20px;

  @include mq(phone-wide,max) {
    font-size: 52px;
  }
}

h4,.h4 {
}

h5,.h5 {
}

.ttl {
}

html,body,p,li,div,input {
  font-family: $bfont;
  font-weight: 400;
}

p {
  font-family: $hfontalt;
  font-size: 18px;
  font-style: italic;
  line-height: 1.8;

  @include mq(phone-wide,max) {
    font-size: 16px;
  }
}

p:empty {
  display: none;
}

li {
}

a,span {
  font-family: inherit;
}

textarea,button {
  font-family: $bfont;
}