// #Button-Styles

.btn,input[type="submit"] {
  cursor: pointer;
  display: table;
  text-transform: uppercase;
  text-align: center;
  font-size: 18px;
  line-height: 1;
  font-family: $bfont;
  font-weight: $med;
  color: $white;
  background: $blue;
  padding: 14px 30px;
  letter-spacing: .12em;
  margin: 20px 0;
  appearance: none;
  box-shadow: none;
  border: 2px solid $blue;
  transition: all .25s ease-in-out;

  @include mq(phone-wide,max) {
  	font-size: 14px;
  	padding: 12px 24px;
  }

	&--inline {
	  display: inline-table;

		&:not(:last-of-type) {
		  margin-right: 15px;
		  margin-bottom: 15px;
		}
	}

	&:hover {
		background: transparent;
		color: $blue;
	}
}